.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #00CC22;
    min-height: 100vh;
    padding: 30px;
}

.wrapper {
    width: 1280px;
    background-color: white;
    border-radius: 6px;
    overflow: hidden;
    color: black;
    padding: 30px;
    box-sizing: border-box;
}

.number {
    font-size: 14px;
    color: #000;
    width: 24px;
    text-align: center;
    margin-right: 8px;
}